.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000000000000000000000;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  width: 95%;
  height: 95%;
}

.modal-body {
  padding: 2rem;
  width: 100%;
  border-radius: 5px;
  background: #fff;
  height: 100%;
}
